.text-overflow {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.close-Icon {
  color: #e8e8e8;
  font-size: 12px;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  cursor: pointer;
}
.close-Icon:hover {
  color: rgba(0, 0, 0, 0.335);
}
.RA-navigator {
  min-height: 100%;
  background-color: var(--component-background);
  box-shadow: 0px 0px 28px var(--primary-lightener-extra);
  z-index: 3;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  display: flex;
  flex-flow: column;
  flex: 0 0 255px;
  max-width: 255px;
  min-width: 255px;
  width: 255px;
  position: relative;
  overflow: hidden;
}
.RA-navigator.RA-navigator-fixSiderBar {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
}
.RA-navigator.RA-navigator-collapsed {
  flex: 0 0 80px;
  max-width: 80px;
  min-width: 80px;
  width: 80px;
}
.RA-navigator.RA-inlineLayout-navigator.RA-navigator-fixSiderBar {
  top: 60px;
  height: calc(100vh - 88px);
  min-height: unset;
}
.RA-navigator.RA-splitLayout-navigator {
  z-index: 8;
}
.RA-menu {
  margin-top: 24px;
  flex: 1 1;
  overflow: auto;
  overflow-x: hidden;
  border: 0 !important;
}
.RA-menu::-webkit-scrollbar {
  display: none !important;
}
.RA-menu .RA-menuIcon {
  margin-right: 12px !important;
  display: inline-block;
}
.RA-menu .RA-menuIcon > span {
  margin: 0 !important;
}
.RA-menu.RA-menu-horizontal {
  margin-top: 0;
  line-height: 60px;
  height: 60px;
}
.RA-menu.RA-menu-horizontal .RA-antd-subMenu-title,
.RA-menu.RA-menu-horizontal .RA-antd-menuItem-title {
  border: 0 !important;
  height: 100%;
  line-height: 60px;
  top: 0px !important;
}
.RA-menu.RA-menu-horizontal .RA-antd-menuItem-title {
  padding: 0 20px;
}
.RA-menu.RA-menu-horizontal .RA-antd-subMenu-title > div {
  height: 60px;
}
.RA-menu.RA-menu-collapsed .RA-antd-subMenu-title,
.RA-menu.RA-menu-collapsed .RA-antd-menuItem-title {
  display: inline-block;
  max-width: 0;
  opacity: 0;
}
.RA-menu.RA-menu-collapsed .RA-menuIcon {
  display: block;
  min-width: 14px;
}
.RA-menu .RA-antd-subMenu,
.RA-menu .RA-antd-menuItem {
  -webkit-transition: color 0.3s cubic-bezier(0.645, 0.045, 0.355, 1), border-color 0.3s cubic-bezier(0.645, 0.045, 0.355, 1), background 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: color 0.3s cubic-bezier(0.645, 0.045, 0.355, 1), border-color 0.3s cubic-bezier(0.645, 0.045, 0.355, 1), background 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
}
